<template>
  <div class="page">
    <div class="bg">
      <!--      <img :src="'https://picsum.photos/'+sw+'/'+sh+'?grayscale'">-->
      <img :src="bginfo.pic">
      <div class="text">
        <span>{{bginfo.picname}}</span>
        <span>{{bginfo.picadd}}</span>
      </div>
    </div>
    <div class="content" style="flex-direction: column">
      <div class="top">
        <router-link tag="a" :to="'/articles/'+articles.id">
          <div class="time">{{articles.time}}</div>
          <div class="title">{{articles.title}}</div>
          <div class="img"><img :src="articles.tpic"></div>
        </router-link>
      </div>
      <div class="top">
        <router-link tag="a" :to="'/lifes/'+lifes.id">
          <div class="time">{{lifes.time}}</div>
          <div class="title">{{lifes.title}}</div>
          <div class="img"><img :src="lifes.pic"></div>
        </router-link>
      </div>
    </div>
  </div>
</template>
<script>
export default {
    name: 'index',
    data() {
        return {
            sw: window.innerWidth,
            sh: window.innerHeight,
            bginfo: {},
            articles: {},
            lifes: {},
        }
    },
    created() {
        fetch('https://www.fastmock.site/mock/5f854ec44ed74b246903c6e75806ec14/api/bgimg').then((rs) => {
            return rs.json()
        }).then((data) => {
            let xh = Math.floor(Math.random() * data.length)
            this.bginfo = data[xh]
        })
        fetch('https://www.fastmock.site/mock/5f854ec44ed74b246903c6e75806ec14/api/article').then((rs) => {
            return rs.json()
        }).then((data) => {
            this.articles = data[0]
        })
        fetch('https://www.fastmock.site/mock/5f854ec44ed74b246903c6e75806ec14/api/life').then((rs) => {
            return rs.json()
        }).then((data) => {
            this.lifes = data[0]
        })


    },
    methods: {},
    computed: {}
}
</script>

